import React,{useState} from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import '../../assets/styles/modals/ModalCreateLocalStorage.scss'

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const ModalCreateLocalStorage = ()  => {
  const [open, setOpen] = useState(false);
  const [value,setValue] = useState("")
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
 
  function handleSubmit(e) {
    e.preventDefault();
    localStorage.setItem(value,[])
  }

  return (
    <div>
      <Button className="buttonCreate" onClick={handleOpen}>Crear lista</Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        className="modalCreateList"
      >
        <form onSubmit={handleSubmit}>
            <Box sx={style}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
                Crear nueva lista
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                <div className="row">
                    Nombre de lista 
                    <input type="text" id="textList" onChange={(e) => setValue(e.target.value)} />
                </div>
            </Typography>
            <div className="col">
                <button type="submit" value="submit">Crear</button>
            </div>
            <div className="row">

            </div>
            </Box>
        </form>
      </Modal>
    </div>
  );
}

export default ModalCreateLocalStorage;
